.wc-booster-cta-content-wrapper {
    position: relative;
    overflow: hidden;
}
.wp-block-wc-booster-call-to-action {
    overflow: hidden;
}
.wc-booster-cta-overlay {
    position: absolute;
    left: 0;
    height: 100%;
    width: 100%;
    top: 0;
}

.wc-booster-cta-content {
    position: relative;
    z-index: 9;
    h2,p {
        margin: 0;
    }
}

.wc-booster-align-center {
    &.wc-booster-cta-content {
        margin: 0 auto;
        text-align: center;
    }
}

.wc-booster-align-right {
    text-align: right;
}
.wc-booster-align-left {
    text-align: left;
}

.content-alignment-right {
    .wc-booster-cta-content {
        margin-left: auto;
    }
}

.content-alignment-left {
    .wc-booster-cta-content {
        margin-right: auto;
    }
}
